
.logo-img{
    height: 60px;
    width: 90px;
}
.app{
    width: 100%;
    height: 100%;
}
.header{
    display: flex;
    width: 100%;
    justify-content: space-between;
    box-sizing: border-box;
    background-color: rgb(255, 255, 255);
    /* align-items: center; */
    padding: 0px 70px;
    box-shadow: 10px 6px 1cap rgb(192, 100, 100);

}
.nav-ul{
    display: flex;
    list-style-type: none;
    padding-top: 0;
    /* margin-top: 20px; */
    font-size: 18px;
    padding-right: 200px;
    align-items: center;
    justify-content: center;
}
.nav-ul > li{
    padding: 10px 40px;
    margin: 8px;  
    width: 100px;
    color: rgb(36, 36, 36);
    cursor: pointer;  
}



.login:hover{
    background-color: orange;
    color: white;
}
.nav-ul > li:hover{
    color: orange;
}
.search{
    padding: 10px;
}
.res-card{
    width: 270px;
    height: 390px;
    /* border: 1px solid black; */
    border-radius: 10px;
    background-color: white;
    margin: 5px;
    font-family: sans-serif;
    
}
.res-card:hover{
    transform: scale(1.05);
    transition-duration: 400ms;
}
.res-sample{
    width: 100%;
    height: 55%;
    border-radius: 10px;
}

.res-card > .cardInfo > h3{
    margin: 6px;
    padding-left: 5px;
    font-size: 17px;
    font-weight: 500;
    color: rgba(2, 6, 12, 0.75);
}
.res-card > .cardInfo > .ratingEta >h4{
    margin: 0;
    font-size: 17px;
    color: rgb(71, 71, 71);
    font-weight: 500;
}
.res-card > .cardInfo > .ratingEta >p{
    margin: 0;
    font-size: 15px;
    color: rgb(56, 56, 56);
}
.res-card > .cardInfo > p{
    margin: 0;
    padding-left: 5px;
    font-size: 14px;
    font-weight: 100;
    word-break: break-all;
    white-space: normal;
    color: grey;
}
.res-card > h4{
    margin-top: 0px;
    margin-bottom: 5px;
    padding-left: 5px;
    word-break: break-all;
    white-space: normal;
}
.ratingEta{
    display: flex;
    margin-left: 5px;
}
.res-container{
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
    justify-content: center;
    align-items: center;
}
.cardInfo{
    display: flex;
    flex-direction: column;
}
.star{
    color: green;
    padding-right: 3px;
}

/* carousel div */


.carouselDiv{
    display: flex;
    flex-direction: column;
}
.cardDiv{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-bottom: 50px;
}
.card{
    height: 135px;
    width: 135px;
}
.card > img{
    height: 100%;
    width: 100%;
}
.filter-btn{
    margin-top: 0px;
    margin-bottom: 10px;
    margin-left: 10px;
    cursor: pointer;
}




/* Shimmer UI */

.shimmer{
    display: flex;
    flex-direction: column;
}
.shimmerCardContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.shimmerCard0{
    height: 150px;
    width: 125px;
    background-color: #e0e0e0;
    border-radius: 5px;
    margin: 6px;
}
.shimmerContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 15px;
    
}
.shimmerCard{
    width: 270px;
    height: 190px;
    /* border: 1px solid black; */
    border-radius: 10px;
    /* border: 1px solid black; */
    background-color: #e0e0e0;
    margin: 5px;
}

.search{
    display: flex;
    justify-content: center;
    align-items: center;
}
.searchBox{
    border-radius: 12px;
    margin-right: 10px;
}
.searchBtn{
    border-radius: 12px;
    border: none;
    background-color: rgb(252, 153, 60);
}
.searchBtn:hover{
    background-color: orange;
    transform: scale(1.1);
}
